// Particles 1
const particlesGeometry1 = new THREE.BufferGeometry()
const count1 = 50000

const positions1 = new Float32Array(count1 * 3) // Multiply by 3 because each position is composed of 3 values (x, y, z)

for (
    let i = 0;
    i < count1 * 3;
    i++ // Multiply by 3 for same reason
) {
    positions1[i] = (Math.random() - 0.5) * 10 // Math.random() - 0.5 to have a random value between -0.5 and +0.5
}

particlesGeometry1.setAttribute(
    'position',
    new THREE.BufferAttribute(positions1, 3)
) // Create the Three.js BufferAttribute and specify that each information is composed of 3 values

//particlesMaterial.map = particleTexture

//particle material
