// model 1
var modelgltf1;
let mixer1 = null;

const loader = new GLTFLoader(loadingManager).setPath('models/')

loader.load(
  'venice3_ani.glb',
  function (gltf) { 
    modelgltf1 = gltf.scene;
    modelgltf1.position.x = -7
    modelgltf1.position.y = 0
    modelgltf1.position.z = -7.95
    modelgltf1.rotation.y = 2.029
    modelgltf1.scale.set(0.07, 0.07, 0.07)
    scene.add(modelgltf1)

    mixer1 = new THREE.AnimationMixer(modelgltf1)
    const action = mixer1.clipAction(gltf.animations[0])
    action.play()

    gui
.add(modelgltf1.rotation, 'y')
.min(- 40)
.max(40)
.step(0.001)
.name('Model1-y')
gui
.add(modelgltf1.position, 'z')
.min(- 40)
.max(40)
.step(0.01)
.name('Model1-Z')
gui
.add(modelgltf1.position, 'x')
.min(- 40)
.max(40)
.step(0.01)
.name('Model1-X')

  },
  
  undefined,
  function (error) {
    console.error(error)
  }
   
  
)

