function onKeyDown(event) {
    switch (event.code) {
    case 'ArrowUp':
    case 'KeyW':
        moveForward = true
        break
    case 'ArrowLeft':
    case 'KeyA':
        moveLeft = true
        break
    case 'ArrowDown':
    case 'KeyS':
        moveBack = true
        break
    case 'ArrowRight':
    case 'KeyD':
        moveRight = true
        break
    default:
        break
    }
}

function onKeyUp(event) {
    switch (event.code) {
    case 'ArrowUp':
    case 'KeyW':
        moveForward = false
        break
    case 'ArrowLeft':
    case 'KeyA':
        moveLeft = false
        break
    case 'ArrowDown':
    case 'KeyS':
        moveBack = false
        break
    case 'ArrowRight':
    case 'KeyD':
        moveRight = false
        break
    default:
        break
    }
}

document.addEventListener('keydown', onKeyDown, false)
document.addEventListener('keyup', onKeyUp, false)

