const clock = new THREE.Clock()

// simulate a timestamp difference(time-prevTime), used together with calcDelta to calculate delta
function randInt(min, max) {
    return Math.floor(Math.random() * (max - min) + min)
}

function calcDelta(x) {
    return x / 1000
}

// handles pointer movement once cursor is 'locked'
const pointerCtrl = () => {
    if (controls.isLocked) {
        const delta = calcDelta(randInt(26, 36))
        direction.x = Number(moveRight) - Number(moveLeft)
        direction.z = Number(moveForward) - Number(moveBack)
        direction.normalize()
        velocity.x = direction.x * 2 * delta
        velocity.z = direction.z * 2 * delta

        controls.moveForward(velocity.z)
        controls.moveRight(velocity.x)
    }
}

const tick = () => {
    pointerCtrl()
    const elapsedTime = clock.getElapsedTime()
    //const deltaTime = clock.getDeltaTime()

    //Animate Venice twirl?
    // logo.position.x = 2.5 + Math.cos(elapsedTime) * 1
    // logo.position.y = 2.5 + Math.sin(elapsedTime) * 0.5

    // logo.position.x = 2.5 + Math.cos(elapsedTime) * 0.3
    // logo.position.y = 2.5 + Math.sin(elapsedTime) * 0.5
    //video move
    // videoplane.position.x = -13 + Math.cos(elapsedTime) * 1
    // videoplane.position.z = -35.5 + Math.sin(elapsedTime) * 0.5
    // videoplane.position.y += 0.0001

    // videoplane.rotation.x = 0 + Math.cos(elapsedTime) * 0.2
    // videoplane.rotation.y = 0.18
        // VID1
    videoScreen1.position.x = 10 + Math.cos(elapsedTime) * 0.1
    videoScreen1.position.z = -9.5 + Math.sin(elapsedTime) * 0.1
    videoScreen1.position.y += 0.0001
    videoScreen1.rotation.x = 0 + Math.cos(elapsedTime) * 0.05
        // VID 2
    videoScreen2.position.x = -16 + Math.cos(elapsedTime) * 0.1
    videoScreen2.position.z = -34 + Math.sin(elapsedTime) * 0.1
    videoScreen2.position.y += 0.0001
    videoScreen2.rotation.x = 0 + Math.cos(elapsedTime) * 0.05

     // VID 3
     videoScreen3.position.x = 5 + Math.cos(elapsedTime) * 0.1
     videoScreen3.position.z = -49.5 + Math.sin(elapsedTime) * 0.1
     videoScreen3.position.y += 0.0001
     videoScreen3.rotation.x = 0 + Math.cos(elapsedTime) * 0.05
    //videoScreen.rotation.y = 0.18
    //particles move

    particles1.rotation.y += 0.001
    particles1.position.y += -0.001

    particles2.rotation.z += -0.001
    particles2.position.x += 0.001

   // modelgltfc.position.y += 0.001;

    //animate model?
    if (mixer1) {
        mixer1.update(0.002 * Math.sin(elapsedTime))
    }
    if (mixer2) {
        mixer2.update(0.01 * Math.cos(elapsedTime))
    }
    if (mixer3) {
        mixer3.update(0.0005 * Math.sin(elapsedTime))
    }
    if (mixer4) {
        mixer4.update(0.01 * Math.cos(elapsedTime))
    }
    if (mixerb1) {
        mixerb1.update(0.01 * Math.cos(elapsedTime))
    }
    if (mixerb2) {
        mixerb2.update(0.01 * Math.cos(elapsedTime))
    }
    if(mixera)
    {
        mixera.update(Math.cos(-elapsedTime*0.00005)* (elapsedTime*0.00005));
    }
    if(mixerc)
    {
        mixerc.update(0.1 * Math.cos(elapsedTime));
    }

    //modelgltf1.position.x = 0 + Math.cos(elapsedTime) * 1;

    //animate water
    water.material.uniforms['time'].value += 1.0 / 240.0
    const time = performance.now() * 0.001
    water.position.y += 0.0001
    camera.position.y += 0.0001
    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()
